.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tab--hidden {
  display: none;
}

.switcher {
  font-size: 1.5rem;
  border: 0;
  margin: 10px;
  background-color: #ff91bc;
  border-radius: 30px;
  color: white;
  width: 55px;
  height: 55px;
}

.switcher:hover {
  background-color: #ff6fa7;
}
